import Sequelize from 'sequelize';
import { sequelize } from '../database/database';
import { CPF_REGEX } from '../Utils/Utils';

const User = sequelize.define('user', {

    full_name: {
        type: Sequelize.STRING
    },
    cpf: {
        type: Sequelize.STRING,
        primaryKey: true
    },
    password: {
        type: Sequelize.STRING
    }

});

export default User;